<template>
  <div ref="svgRoot" class="svgRoot">
    <lines
      ref="lines"
      :is-draw-line="isDrawLine"
      :lines="lines"
      :nodes="nodes"
      :events="events"
      :checked="checked"
      :config="config"
    ></lines>
    <nodes
      :is-draw-line="isDrawLine"
      :nodes="nodes"
      :checked="checked"
      :config="config"
    ></nodes>
    <events
      :is-draw-line="isDrawLine"
      :nodes="nodes"
      :events="events"
      :checked="checked"
      :config="config"
    ></events>
  </div>
</template>
<script>
import Lines from "./Lines";
import Events from "./Events";
import subprocess from "./Subprocess";
import { getAttributes } from "./common";
import { BaseInfoProperties } from "./propertySource";
export default {
  name: "FlowContent",
  components: {
    Lines,
    Events,
    subprocess,
  },
  props: {
    flowInfo: {
      type: Object,
      default() {
        return {};
      },
    },
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    lanes: {
      type: Object,
      default() {
        return {};
      },
    },
    events: {
      type: Array,
      default() {
        return [];
      },
    },
    isDrawLine: {
      type: Boolean,
      default() {
        return false;
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    config: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
  watch: {
    flowInfo(val) {
      if (val) {
        this.init();
      }
    },
  },
  created() {},
  mounted() {},
  methods: {
    init() {
      this.initFlow();
    },
    initFlow() {
      //
      flow = {
        attributes: Object.assign(
          getAttributes(BaseInfoProperties()),
          this.flowInfo
        ),
      };
    },
  },
};
</script>
