import { LaneDirection } from './tools';

import { getAttributes } from "./common";
import {
  CorridorProperties,
} from "./propertySource";

//
const Lane = function (opt) {
  var options = Object.assign({ size: 260, direction: LaneDirection.vertical }, opt);
  this.id = options.id || null;
  this.name = options.name || "";
  this.index = options.index;
  this.direction = options.direction;
  this.isRender = false;
  this.size = options.size;

  if (!options.attributes) {
    this.attributes = getAttributes(CorridorProperties());
    this.attributes.Name = this.name;
    this.attributes.ID = this.id;
  } else {
    this.attributes = { ...options.attributes };
  }
}
export default Lane;