<template>
  <div class="ds_attr_root">
    <div v-show="status === 'base'" class="base">
      <p class="title">
        <span></span>
        <el-button class="el-button-all" size="mini" round @click="status = 'advanced'">
          <span></span>
          <i class="el-icon-arrow-right"></i>
        </el-button>
      </p>
      <div class="el-collapse-item__content">
        <el-form
          v-for="(item, itemIndex) in data"
          :key="itemIndex"
          class="colla-content"
          label-position="top"
          label-width="80px"
        >
          <div v-for="(attr, index) in item.attributes" :key="index" class="property-item">
            <el-form-item v-if="!attr.hide && attr.isBase">
              <span slot="label" class="property-item-name">
                {{ attr.name }}
                <DsfIcon
                  v-if="attr.desc"
                  style="margin-left: 5px; cursor: pointer;"
                  name="bangzhuzhongxin"
                  @click.native="showHelp(attr)"
                ></DsfIcon>
                <el-dialog
                  :visible.sync="attr.showHeplerMark"
                  :title="attr.name"
                  :append-to-body="true"
                  :width="'500px'"
                >
                  <div style="max-height: 500px; min-height: 300px; overflow: auto;">
                    <span style="display: inline-block; line-height: 1.5em;" v-html="attr.desc"></span>
                  </div>
                </el-dialog>
              </span>
              <div v-if="attr.nodeType === 'flow' && attr.nodeName2 === 'Version'" class="inline">
                <el-input-number
                  v-model="attr.value0"
                  controls-position="right"
                  :min="minValue(attr, 0)"
                  @input="changeVesion(attr, 0)"
                ></el-input-number>
                <span>.</span>
                <el-input-number
                  v-model="attr.value1"
                  controls-position="right"
                  :min="minValue(attr, 1)"
                  @input="changeVesion(attr, 1)"
                ></el-input-number>
                <span>.</span>
                <el-input v-model="attr.value2" disabled="disabled"></el-input>
              </div>
              <div v-if="attr.unit" class="inline">
                <el-input-number
                  v-model="attr.value"
                  controls-position="right"
                  :min="1"
                  :max="attr.max"
                  @input="changeProperty(attr)"
                ></el-input-number>
                <span class="unit">{{ attr.unit }}</span>
              </div>
              <el-input
                v-if="show(attr)"
                v-model="attr.value"
                :disabled="!attr.editor"
                @input="changeProperty(attr)"
              ></el-input>
              <el-input
                v-if="attr.editor === 'textarea'"
                v-model="attr.value"
                type="textarea"
                placeholder=""
                @input="changeProperty(attr)"
              ></el-input>
              <el-select
                v-if="attr.editor && attr.editor.type === 'combobox'"
                v-model="attr.value"
                :name="`${attr.nodeName1 + '_' + attr.nodeName2}`"
                @change="changeProperty(attr)"
              >
                <el-option
                  v-for="(option, i) in attr.editor.options.data"
                  :key="i"
                  :label="option.text"
                  :value="option.value"
                ></el-option>
              </el-select>
              <!---->
              <el-radio-group
                v-if="attr.editor && attr.editor.type === 'radio'"
                v-model="attr.value"
                @change="changeProperty(attr)"
              >
                <el-radio v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                  {{ option.text }}
                </el-radio>
              </el-radio-group>
              <!---->
              <el-checkbox-group
                v-if="attr.editor && attr.editor.type === 'checkbox'"
                v-model="attr.value"
                @change="changeProperty(attr)"
              >
                <el-checkbox v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                  {{ option.text }}
                </el-checkbox>
              </el-checkbox-group>
              <!---->
              <DsfButton
                v-if="attr.type === 'dialog'"
                type="auxiliary"
                icon="shezhi"
                :class="attr.class"
                @click="doOpenDialog(attr)"
              >
                
              </DsfButton>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
    <div v-show="status === 'advanced'" class="advanced">
      <p class="title">
        <span></span>
        <el-button class="el-button-base" size="mini" round @click="status = 'base'">
          <i class="el-icon-arrow-left"></i>
          <span></span>
        </el-button>
      </p>
      <el-collapse v-model="activeNames" accordion>
        <el-collapse-item v-for="item in data" :key="item.name" :title="item.name" :name="item.name">
          <el-form label-position="top" label-width="80px" class="colla-content">
            <div v-for="(attr, index) in item.attributes" :key="index" class="property-item">
              <el-form-item v-if="!attr.hide">
                <span slot="label" class="property-item-name">
                  {{ attr.name }}
                  <!-- <el-popover placement="bottom" width="210" trigger="click" :content="attr.desc">
                    <i slot="reference" class="el-icon-question"></i>
                  </el-popover>-->
                  <DsfIcon
                    v-if="attr.desc"
                    style="margin-left: 5px; cursor: pointer;"
                    name="bangzhuzhongxin"
                    @click.native="showHelp(attr)"
                  ></DsfIcon>
                  <el-dialog
                    :visible.sync="attr.showHeplerMark"
                    :title="attr.name"
                    :append-to-body="true"
                    :width="'500px'"
                  >
                    <div style="max-height: 500px; min-height: 300px; overflow: auto;">
                      <span style="display: inline-block; line-height: 1.5em;" v-html="attr.desc"></span>
                    </div>
                  </el-dialog>
                </span>
                <div v-if="attr.nodeType === 'flow' && attr.nodeName2 === 'Version'" class="inline">
                  <el-input-number
                    v-model="attr.value0"
                    controls-position="right"
                    :min="minValue(attr, 0)"
                    @input="changeVesion(attr, 0)"
                  ></el-input-number>
                  <span>.</span>
                  <el-input-number
                    v-model="attr.value1"
                    controls-position="right"
                    :min="minValue(attr, 1)"
                    @input="changeVesion(attr, 1)"
                  ></el-input-number>
                  <span>.</span>
                  <el-input v-model="attr.value2" disabled="disabled"></el-input>
                </div>
                <div v-if="attr.unit" class="inline">
                  <el-input-number
                    v-model="attr.value"
                    controls-position="right"
                    :min="1"
                    :max="attr.max"
                    @input="changeProperty(attr)"
                  ></el-input-number>
                  <span class="unit">{{ attr.unit }}</span>
                </div>
                <el-input
                  v-if="show(attr)"
                  v-model="attr.value"
                  :disabled="!attr.editor"
                  @input="changeProperty(attr)"
                ></el-input>
                <el-input
                  v-if="attr.editor === 'textarea'"
                  v-model="attr.value"
                  type="textarea"
                  placeholder=""
                  @input="changeProperty(attr)"
                ></el-input>
                <el-select
                  v-if="attr.editor && attr.editor.type === 'combobox'"
                  v-model="attr.value"
                  :name="`${attr.nodeName1 + '_' + attr.nodeName2}`"
                  @change="changeProperty(attr)"
                >
                  <el-option
                    v-for="(option, i) in attr.editor.options.data"
                    :key="i"
                    :label="option.text"
                    :value="option.value"
                  ></el-option>
                </el-select>
                <!---->
                <el-radio-group
                  v-if="attr.editor && attr.editor.type === 'radio'"
                  v-model="attr.value"
                  @change="changeProperty(attr)"
                >
                  <el-radio v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                    {{ option.text }}
                  </el-radio>
                </el-radio-group>
                <!---->
                <el-checkbox-group
                  v-if="attr.editor && attr.editor.type === 'checkbox'"
                  v-model="attr.value"
                  @change="changeProperty(attr)"
                >
                  <el-checkbox v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                    {{ option.text }}
                  </el-checkbox>
                </el-checkbox-group>
                <!---->
                <DsfButton
                  v-if="attr.type === 'dialog'"
                  type="auxiliary"
                  icon="shezhi"
                  :class="attr.class"
                  @click="doOpenDialog(attr)"
                >
                  
                </DsfButton>
              </el-form-item>
            </div>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>
<script>
import { MessageBox, Message } from "element-ui";
import {
  BaseInfoProperties,
  NodeProperties,
  UserTaskNodeProperties,
  ChildTaskProperties,
  StartNodeProperties,
  endNodeProperties,
  ScriptNodeProperties,
  JumpNodeProperties,
  LineProperties,
  WebTaskProperties,
  MessageTaskProperties,
  JudgementNodeProperties,
  CorridorProperties,
  BoundaryEventProperties,
  GetInlineTypeData,
  GetIsSelected,
} from "./propertySource";
import Line from "./Line";
import Lane from "./Lane";
import Node from "./Node";
import BoundaryEvent from "./BoundaryEvent";
import { deepClone } from "./common";
import DsfDesignerDialog from "_platform/dsf/designer/propertyEditor/dsfDesignerDialog";

export default {
  name: "FlowInfo",
  components: {
    DsfDesignerDialog,
  },
  props: {
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    flowInfo: {
      type: Object,
      default() {
        return {};
      },
    },
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      activeNames: ["1"],
      curSelectObj: null,
      curNodeObj: null,
      curElement: null,
      AppInfo: null,
      data: [],
      status: "base",
      showHeplerMark: false,
    };
  },
  computed: {
    minValue() {
      return (attr, index) => {
        if (index === 0) {
          return Number(attr.oldValue.split(".")[0]);
        }
        if (attr.value0 > Number(attr.oldValue.split(".")[0]) && index === 1) {
          return 0;
        }
        return Number(attr.oldValue.split(".")[1]);
      };
    },
  },
  watch: {
    checked(ui) {
      this.loadPropertyWindow(ui);
    },
  },
  mounted() {
    this.$bus.$on("updateNodeName", (ui, bool) => {
      if (!bool) {
        this.loadPropertyWindow(ui);
      }
    });
  },
  methods: {
    show(attr) {
      return (
        (!attr.nodeType && attr.nodeName2 !== "Version" && !attr.editor && attr.type !== "dialog") ||
        (!attr.unit && attr.editor === "text") ||
        attr.editor === "numberbox"
      );
    },
    loadPropertyWindow(obj) {
      if (Object.keys(obj).length === 0) {
        return;
      }
      this.curSelectObj = obj || null;
      this.curNodeObj = !obj ? null : obj.attributes;
      this.loadPropertyDom(obj ? this.setPropertyData(obj) : []);
    },
    showHelp(attr) {
      this.$set(attr, "showHeplerMark", true);
    },
    setPropertyData(obj) {
      var pro = null;
      if (obj instanceof Node) {
        if (obj.type == "101") {
          pro = UserTaskNodeProperties();
        } else if (obj.type == "102") {
          pro = ChildTaskProperties();
        } else if (obj.type == "103") {
          const config = {
            name: "",
            Type: "",
            language: "java",
          };
          pro = ScriptNodeProperties(config);
        } else if (obj.type === "104") {
          pro = WebTaskProperties();
        } else if (obj.type === "105") {
          pro = MessageTaskProperties();
        } else if (obj.type === "106") {
          const config = {
            name: "python",
            Type: "python",
            language: "python",
          };
          pro = ScriptNodeProperties(config);
        } else if (obj.type === "107") {
          const config = {
            name: "shell",
            Type: "shell",
            language: "shell",
          };
          pro = ScriptNodeProperties(config);
        } else if (obj.type === "108") {
          const config = {
            name: "SQL",
            Type: "SQL",
            language: "SQL",
          };
          pro = ScriptNodeProperties(config);
        } else if (obj.type == "201") {
          pro = StartNodeProperties();
        } else if (obj.type == "299") {
          pro = endNodeProperties();
        } else if (obj.type == "301") {
          pro = JudgementNodeProperties();
        } else if (obj.type == "302") {
          pro = JumpNodeProperties();
        } else {
          pro = NodeProperties();
        }
      } else if (obj instanceof Line) {
        pro = LineProperties();
      } else if (obj instanceof Lane) {
        pro = CorridorProperties();
      } else if (obj instanceof BoundaryEvent) {
        pro = BoundaryEventProperties();
      } else {
        // 
        pro = BaseInfoProperties();
      }
      var attr = obj.attributes;
      // 
      var group = null;
      var groupIndex = -1;
      pro.json = [];
      for (let i = 0; i < pro.rows.length; i++) {
        if (pro.rows[i].nodeLevel == 1) {
          // 
          pro.rows[i].value = attr[pro.rows[i].nodeName];
        } else if (pro.rows[i].nodeLevel == 2) {
          // -
          if (attr[pro.rows[i].nodeName1]) {
            pro.rows[i].value = attr[pro.rows[i].nodeName1][pro.rows[i].nodeName2];
          } else {
            pro.rows[i].value = "";
          }
        } else if (pro.rows[i].nodeLevel == 3) {
          //  - 
          if (attr[pro.rows[i].nodeName1] && attr[pro.rows[i].nodeName1][pro.rows[i].nodeName2]) {
            pro.rows[i].value = attr[pro.rows[i].nodeName1][pro.rows[i].nodeName2][pro.rows[i].nodeName3];
          } else {
            pro.rows[i].value = "";
          }
        }
        if (pro.rows[i].group != group) {
          groupIndex++;
          group = pro.rows[i].group;
          pro.json[groupIndex] = {
            name: group,
            attributes: [],
          };
        }
        pro.json[groupIndex]["attributes"].push(pro.rows[i]);
      }
      return pro.json;
    },
    loadPropertyDom(data) {
      let rows = [];
      data.forEach((item) => {
        item.attributes.forEach((attr) => {
          if (attr.editor && attr.editor.type === "checkbox") {
            attr.value = attr.value || [];
          } else if (attr.editor && attr.editor.type !== "checkbox") {
            attr.value = attr.editor && attr.editor.options && attr.value ? Number(attr.value) : attr.value;
          } else if (attr.nodeType === "flow" && attr.nodeName2 === "Version") {
            // 
            attr.oldValue = attr.value;
            attr.value0 = attr.value.split(".")[0];
            attr.value1 = attr.value.split(".")[1];
            attr.value2 = attr.value.split(".")[2];
          } else {
            attr.value = attr.value;
          }

          if (attr.showRob) {
            rows.push(attr);
          }
        });
      });
      this.data = data;

      //  /  
      if (rows && rows.length > 0) {
        rows.forEach((row) => {
          this.showRob(row);
        });
      }
      this.activeNames = [this.data[0].name];
    },
    changeVesion(attr, index) {
      if (attr) {
        if (index === 0) {
          attr.value1 = 0;
          attr.value2 = "0001";
        }
        attr.value = attr.value0 + "." + attr.value1 + "." + attr.value2;
        this.changeProperty(attr);
      }
    },
    changeProperty(rowData) {
      if (rowData.nodeName2 && rowData.nodeName2 == "Name") {
        if (this.curSelectObj instanceof Node) {
          this.curSelectObj.name = rowData.value;
          // this.$bus.$emit("updateName", this.curSelectObj);
          this.$bus.$emit(`updateNodeName${this.isSub ? this.parentNode.id : ""}`, this.curSelectObj, true);
        }
        if (this.curSelectObj instanceof Lane) {
          this.curSelectObj.name = rowData.value;
        }
        if (this.curSelectObj instanceof Line) {
          this.curSelectObj.name = rowData.value;
        }
      }
      if (rowData.showRob && rowData.showRob == "GatherRule") {
        //  
        if (this.curSelectObj instanceof Line) {
          this.curSelectObj.type = rowData.value;
        }
      }
      if (rowData.nodeName1 === "InLine" && rowData.nodeName2 === "Type") {
        let name = GetInlineTypeData(rowData.value);
        let RangeRule = [];
        const Rule = this.curNodeObj.InLine.RangeRule && this.curNodeObj.InLine.RangeRule.Rule;
        if (Rule && Rule.length > 0) {
          Rule.forEach((item, index) => {
            RangeRule.push(item.name + "/" + GetIsSelected(item.IsSelected));
          });
          this.curSelectObj.taskMsg = `${name}: ${RangeRule.join(",")}`;
        }
      }
      if (
        rowData.nodeName1 === "InLine" &&
        rowData.nodeName2 === "RangeRule" &&
        rowData.value &&
        rowData.value.Rule &&
        rowData.value.Rule.length > 0
      ) {
        let name = GetInlineTypeData(this.curNodeObj.InLine.Type);
        let RangeRule = [];
        rowData.value.Rule.forEach((item, index) => {
          RangeRule.push(item.name + "/" + GetIsSelected(item.IsSelected));
        });

        this.curSelectObj.taskMsg = `${name}: ${RangeRule.join(",")}`;
      }
      //  
      this.showRob(rowData);
      if (rowData.nodeLevel == 1) {
        this.curNodeObj[rowData.nodeName] = rowData.value;
      } else if (rowData.nodeLevel == 2) {
        this.curNodeObj[rowData.nodeName1][rowData.nodeName2] = rowData.value;
      } else if (rowData.nodeLevel == 3) {
        this.curNodeObj[rowData.nodeName1][rowData.nodeName2][rowData.nodeName3] = rowData.value;
      }
    },
    showRob(rowData) {
      if (rowData.showRob) {
        this.data.forEach((item) => {
          item.attributes.forEach((attr) => {
            const showRob = rowData.showRob.split(",");
            showRob.forEach((field) => {
              if (attr.nodeName2 === field || attr.nodeName3 === field) {
                if (field === "RushInfo") {
                  if (rowData.value === 5) {
                    //
                    Object.assign(attr, {
                      name: "",
                      value: 1,
                      max: 999999,
                      unit: "",
                      hide: false,
                    });
                  } else if (rowData.value === 6) {
                    //
                    Object.assign(attr, {
                      name: "",
                      value: 60,
                      max: 100,
                      unit: "%",
                      hide: false,
                    });
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "GatherRule") {
                  if (rowData.value === 1 || rowData.value === 2) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Params") {
                  if (rowData.value === 0 || rowData.value === 1) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Content") {
                  if (rowData.value === 2) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Value") {
                  if (rowData.value !== 0) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                }
              }
            });
          });
        });
      }
    },
    doOpenDialog(rowData) {
      let data = rowData;
      let params = {
        code: (data.value && data.value.Expression) || "",
        language: "java",
        checked: this.checked,
        value: data.value,
      };
      if (data.config && data.config.content) {
        if (data.config.content === "RangeRule") {
          params.nodes = this.nodes;
          params.value = data.value || {};
        } else if (data.config.content === "Summary") {
          params.lines = this.lines;
          params.value = data.value || {};
        } else if (data.config.content === "SendCondition") {
          params.lines = this.lines;
          params.value = data.value || {};
        } else if (data.config.content === "EventSetting") {
          params.isEvent = true;
        }
      }
      dsf.layer.pc.openDialog({
        title: data.name,
        width: data.config.width,
        height: data.config.height,
        overflow: "hidden",
        content: data.config.content,
        params: params,
        btns: [
          {
            text: "",
            handler: (component) => {
              data.value = component.getContent();
              this.changeProperty(data);
            },
          },
          {
            text: "",
          },
        ],
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.inline {
  display: flex;
  /deep/ .el-input__inner {
    text-align: center;
    padding: 0;
  }
  .el-input-number {
    width: 300px;
    /deep/ .el-input-number__decrease,
    /deep/ .el-input-number__increase {
      width: 20px;
    }
    /deep/ .el-input__inner {
      padding-right: 16px;
    }
  }

  .el-input-number.is-controls-right .el-input__inner {
    padding-right: 34px;
  }
  .unit {
    display: inline-block;
    width: 42px;
    text-align: center;
  }
}
.title {
  height: 50px;
  line-height: 50px;
  text-align: center;
  position: relative;
  .el-button {
    position: absolute;
    top: 12px;
    padding: 7px 10px;
    &.el-button-all {
      right: 5px;
    }
    &.el-button-base {
      left: 5px;
    }
  }
}
.el-select {
  width: 100%;
}
/deep/ .el-checkbox {
  margin-right: 12px;
}
</style>
